.ProjectsView{
    height: 90vh;
    width: 70%;
    position: fixed;
    top:0;
    margin: 2.5%;
    margin-left: 15%;
    background-color: #ffffff;
    z-index: 500;
    display: flex;
    flex-direction: column;
    border-radius: 0.5vw;
    border: rgba(0, 0, 0, 0.2) solid 1px;
}
.ProjectImage{
    background: #ccc;
    position: relative;
    height: 60vh;
    width: 100%;
    background-image: url('./ProjectJoel.png');
    background-repeat: none;
    background-size: 100%;
    border-top-left-radius: 0.5vw;
    border-top-right-radius: 0.5vw;
}
@media (max-width: 600px){
    .ProjectsView{
        height: 90vh;
        width: 95%;
        margin: 2.5%;
    }
}
/* Arrows */
#left, #right{
    background: rgba(0, 0, 0, 0.35);
    color:  white;
    width: 2.5vw;
    height: 2.5vw;
    border-radius: 50%;
    position: absolute;
    cursor: pointer;
}
#left span, #right span{
    position: relative;
    left: 25%;
    top: 14%;
    font-size: 1.2vw;
}
#left:hover, #right:hover{
    width: 2.6vw;
    height: 2.6vw;
    background: rgba(0, 0, 0, 0.6);
    
}
#left{
    left:1vw;
    top:48%;
}
#right{
    top:48%;
    right: 1vw;
}
/* Description styles */
.ProjectDesc h1{
    width: 100%;
    position: relative;
    left: 0;
    margin: 2vw;
}
.ProjectDesc p{
    margin: 2vw;
    font-family: 'Lato';
    font-size: 1.1vw;
}
