*{
  margin: 0;
  padding: 0;
  font-family: 'Big-John';
}

.App{
    display: flex;
    flex-direction: column;
}
.menu-icon{
    position: fixed;
    left: 2vw;
    top: 2vw;
    z-index: 2000;
    cursor: pointer;
}
.App-mobile{
    display: none;
}



/* mobile */
@media (max-width: 700px) {
    .App{
        overflow: hidden;
    }
    .App-mobile{
        display: block;
        width: 100%;
        height: 100vh;
        background: rgb(249, 195, 76);
        position: fixed;
        z-index: 1000000;
    }
    .App-mobile img{
        width: 30%;
        position: absolute;
        top:35%;    
        left: 34%;
        margin-bottom: 2vw;
    }
    .App-mobile h1{
        position: absolute;
        width: 30%;
        top: 55%;
        left: 35%;
        text-align: center;
        font-size: 5vw; 
    }
  }
