/* skill-component */
.skill-component{
    position: relative;
    bottom:60%;
    left: 10%;
    width: 45vw;
    display: flex;
    flex-direction: row;
    margin: 0.7vw;
  }
  .skill-component h1{
    margin-right: 2vw;
    font-size: 2vw;
  }
  .skill-loader{
    width: 90%;
    position: relative;
    right: 0;
  }
  .skill-loader div{
    background: #333;
    width: 100%;
    height: 0.7vw;
    position: absolute;
    top:45%;
    border-radius: 3.5vw;
  }
  .skill-loader div:nth-child(2){
    background: #f9c34c;    
    -webkit-transition: width 2s;    
    transition: width 2s;
  }

